import React from 'react';
import Carddesign from './card';
import { List, ListItem,makeStyles, Divider, Box } from "@material-ui/core";

import { Grid } from '@material-ui/core';
import GetList from './Config';
import Pagination from '@material-ui/lab/Pagination';


const useStyles = makeStyles(theme => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper
  },
  item: {
    padding: theme.spacing(2)
  },
  avatar: { marginRight: theme.spacing(1) },
  paginator: {
    justifyContent: "end",
    padding: "10px"
  }
}));
const Content = () => {

  
    const getCard= GetListObj =>{
      
          return(

            
            <Grid item xs={12} sm={4}>
          
          <Carddesign {...GetListObj} />
          </Grid>
          );
        };
          const classes = useStyles();
          const itemsPerPage = 30;
          const [page, setPage] = React.useState(1);
          const [noOfPages] = React.useState(
            Math.ceil(GetList.length / itemsPerPage)
          );
        
          const handleChange = (event, value) => {
            setPage(value);
          };
    return (
        <Grid container spacing={2}>
          
           {GetList .slice((page - 1) * itemsPerPage, page * itemsPerPage).map(GetListObj => getCard(GetListObj)) }
           {/* <Pagination count={10} /> */}
            <Box component="span">
        <Pagination
          count={noOfPages}
          page={page}
          onChange={handleChange}
          defaultPage={1}
          color="primary"
          size="large"
          showFirstButton
          showLastButton
          classes={{ ul: classes.paginator }}
        />
      </Box>
        </Grid>

    );
}
export default Content;