import React from "react";

import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import CardMedia from "@material-ui/core/CardMedia";
import Pagination from '@material-ui/lab/Pagination';

import {
  createMuiTheme,
  createStyles,
  withStyles,
  makeStyles,
  Theme,
  ThemeProvider,
} from "@material-ui/core/styles";

const useStyles = makeStyles({
  root: {
    marginLeft:"90px",
    minWidth: 200,
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)",
  },
  title: {
    fontSize: 14,
    fontWeight: "bold",
    marginTop:"5px",
    marginRight:"10px"
  },
  title1:{
    fontSize: 14,
    fontWeight: "bold",
    marginTop:"5px",
    marginRight:"10px",
    marginLeft:"6rem"
  },
  pos: {
    marginBottom: 12,
  },
  color:{
    backgroundColor:"#efedf2",
    marginRight:"20px",
    marginLeft:"20px"
  }
});
const ColorButton = withStyles((theme: Theme) => ({
  root: {
    color: theme.palette.getContrastText("#d4934e"),
    backgroundColor: "#d4934e",
    "&:hover": {
      backgroundColor: "#d4934e",
    },
  },
}))(Button);
const Carddesign = (props) => {
  const classes = useStyles();
  
  const {
    title,
    model,
    Date,
    CreatedOn,
    Details,
    Dealer,
    description,
    img,
    logo
  } = props;
  console.log(props.img, "FFFFFFFFFFFFFF");
  return (
    <div>
    <Card className={classes.color}>
      
      <CardMedia style={{ height: "200px" }} image={img} />
      
      <CardContent>
        <div style={{ display: "flex" }}>
          {" "}
          <Typography
            className={classes.title}
            color="textSecondary"
            gutterBottom
          >
            {title}
          </Typography>
          <ColorButton size="small" color="#eb9534">
            For HandOver
          </ColorButton>
          <CardMedia style={{ height: "20px" }} image={logo} />
        </div>

        <Typography className={classes.pos} color="textSecondary">
        {model}
        </Typography>
        <Typography variant="body2" component="p">
        {Date}
          <br />
          {'CreatedOn'}:{CreatedOn}
        </Typography>


        
      </CardContent>
      <CardActions>
      <Button size="small" style={{color:"#eb9534"}}>Bidding Session Details</Button>
        
      </CardActions>
    </Card>
    </div>
  );
};
export default Carddesign;
